import React, { Component } from 'react';
import logo from './logo_white.png';
import './App.css';
import elasticlunr from'elasticlunr';

// Preprocess data
import * as data from './data.json';
const indexedData = {};
for (let item of data) {
  indexedData[item.title] = item;
};
const index = elasticlunr(function () {
  this.addField('title');
  this.addField('address');
  this.addField('offer');
  this.setRef('title');
});
data.map(item => index.addDoc(item));

class Card extends Component {
  render() {
    const { title, address, offer } = this.props;
    return (
      <div className="card">
        <div className="card-header">
          <h5 className="card-title" style={{fontWeight: 'bold'}}>{title}</h5>
        </div>
        <div className="card-body">
          <div className="card-text">{offer}</div>
          <div className="address">
          {address && address.split("\n").map((i,key) => {
            return <p className="card-text" key={key}>{i}</p>;
          })}
          </div>
        </div>
      </div>
    );
  }
}

class Search extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchPath: '',
      found: []
    };

    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleChange(event) {
    this.setState({searchPath: event.target.value});
  }

  handleSubmit(event) {
    // alert('A name was submitted: ' + this.state.searchPath);
    console.log('here');
    const found = index.search(this.state.searchPath, {
      fields: {
        title: {boost: 2},
        offer: {boost: 1},
        address: {boost: 1}
      },
      expand: true,
      bool: 'OR'
    });
    console.log('found', found);
    this.setState({ found });

    event.preventDefault();
  }

  render() {
    return (
      <div className="container">
        <form onSubmit={this.handleSubmit} className="form-inline">
        <div className="form-row">
          <div className="form-group form-input">
            <input type="text" value={this.state.searchPath} onChange={this.handleChange} className="form-control" id="search-input" placeholder="Enter search"></input>
          </div>
          <div className="form-group form-input">
            <button type="submit" className="btn btn-primary" onClick={this.send} style={{marginLeft: '0.5em'}}>Submit</button>
          </div>
        </div>
        </form>
        <div className="cards container">
          <div className="row">
          {this.state.found.map(item => indexedData[item.ref]).map(item => <div key={item.title} className="col sm"><Card {...item}></Card></div>)}
          </div>
        </div>
      </div>
    );
  }
}

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </header>
        <Search></Search>
      </div>
    );
  }
}

export default App;



